@font-face {
  font-family: 'olietta_script_poesia';
  src: url("../fonts/olietta_script_poesia.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}

.allPosts a {
  text-decoration: none;
  font-weight: normal;
  color: #4175b3;
  font-size: 14px;
}

.allPosts a:hover {
  color: #472047;
}

.allPosts div a {
  font-weight: 700;
  font-size: 16px;
  color: #472047;
}

.allPosts ul {
  margin-bottom: 10px;
  margin-left: 20px;
}